<template>
    <div>
        <h1>Регистрация</h1>
        <div>
            <my-input class="inpt" placeholder="Name" v-model="userName"></my-input>
        </div>
        <div>
            <my-input class="inpt" placeholder="Surname" v-model="userSurname"></my-input>
        </div>
        <div>
            <my-input class="inpt" placeholder="SecondName" v-model="userSecondName"></my-input>
        </div>
        <div>
            <my-input class="inpt" placeholder="email" v-model="email"></my-input>
        </div>
        <div>
            <my-button class="btn" @click="reg">Зарегистрироваться</my-button>
        </div>
        <div>
            <my-router-link to="/registration">Назад</my-router-link>
        </div>
        <div style="margin-top: 10px">
            {{  message }}
        </div>
    </div>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        name: 'register',
        data(){
            return{
                userName: '',
                userSurname: '',
                userSecondName: '',
                email: '',
                login: '',
                message: ''
            }
        },
        methods: {
            ...mapActions({
                registration: 'registration/registration',
            }),
            async reg(){
                const obj = { secondname: this.userSecondName, surname: this.userSurname, name: this.userName, email: this.email }
                const resp = await this.registration(obj)
                this.message = resp;
            }
        }
    }
</script>

