import axios from "axios";
import AuthService from "../service/AuthService.js";
import { API_URL } from "../http";

export const registrationModule = {
    state: () => ({
        User: localStorage.getItem('user') || null,
        //User: JSON.parse(localStorage.getItem('user')) || null,
        token: localStorage.getItem('token') || '',
    }),
    getters: {
        isAuthed: state => !!state.token && !!state.User,
        User: state => state.User
    },
    mutations: {
        setToken(state, token) {
            state.token = token;
        },
        setUser(state, User) {
            state.User = User;
        },
    },
    actions: {
        async login({state, commit}, data) {
            try {
                const resp = await AuthService.login(data.email);
                return resp.data;
            } catch (e) {
                console.log(e.response?.data?.message);
            }
        },

         async registration({state, commit, dispatch},data) {
             try {
                 const resp = await AuthService.registration(data)
                 return resp.data;
             } catch (e) {
                 console.log(e.response?.data?.message);
             }
        },

        async activate({state, commit, dispatch, getters}, data) {
            try {
                const response = await AuthService.activate(data.link)
                localStorage.setItem('token', response.data.token);
                const user = {
                    id: response.data.id,
                    name: response.data.name,
                    surname: response.data.surname,
                    email: response.data.email,
                    role: response.data.role
                }
                localStorage.setItem('user', JSON.stringify(user));
                commit('setToken', response.data.token);
                commit('setUser', user);
            }
            catch (e) {
                console.log(e.response?.data?.message);
                return e;
            }
        },

        logout({state, commit}) {
            try {
                localStorage.removeItem('token');
                localStorage.removeItem('user');
                commit('setUser', {});
                commit('setToken', '');
            } catch (e) {
                console.log(e.response?.data?.message);
            }
        },

        async checkAuth({state, commit}) {
            commit('setLoading', true);
            try {
                const response = await axios.get(`${API_URL}/refresh`, {withCredentials: true})
                console.log(response);
                localStorage.setItem('token', response.data.accessToken);
                commit('setAuth', true);
                commit('setUser', response.data.user);
            } catch (e) {
                console.log(e.response?.data?.message);
            } finally {
                commit('setLoading', false);
            }
        }
    },
    namespaced: true
}