import $api from "../http";

export default class PostsService {
    static async getPosts(){
        return $api.get('/labs')
    }
    static async getWorkshops(){
        return $api.get('/workshops/short')
    }
    static async postWorkshop(data){
        return $api.post('/workshops', {сlassroomName: data})
    }
    static async getFacilitiess(){
        return $api.get('/facilities')
    }
    static async postFacility(data){
        return $api.post('/facilities', {numOfPoints: data.num, workshopId: data.workshopId, name: data.name})
    }
    static async postLab(data){
        return $api.post('/labs', {facilityId: data.facilityId, day: data.day})
    }
    static async getTasks(){
        return $api.get('/praktikumtasks')
    }
    static async postTask(data){
        return $api.post('/PraktikumTasks', {description: data.description, labId: data.labId})
    }
    static async postLabUser(data){
        return $api.post('/LabUsers', {userId: data.userId, taskId: data.taskId, date: data.date})
    }
    static async getUserSubscribes(id){
        return $api.get('/LabUsers/user/' + id)
    }
    static async unsubscribe(id){
        return $api.delete('/LabUsers/' + id)
    }
}