<template>
    <div class="common">
        <div class="leftBar">
            <div class="sting">Фамилия: {{ User.surname }}</div>
            <div class="sting">Имя: {{ User.name }}</div>
            <div class="sting">Отчество: {{ User.secondname }}</div>
            <div class="sting">Email: {{ User.email }}</div>
            <div class="sting">Роль: {{ User.role }}</div>
        </div>
        <div>
            <div class="bottomBar">
                <div class="row">Мои записи</div>
                <subscribe-list :subscribes="subscribes" @unsubscribe="unsubscribe"></subscribe-list>
            </div>
            <div class="rightBar">
                <div class="row">Мои оценки</div>
                <div class="list">wddw</div>
            </div>
        </div>
    </div>

</template>

<script>
    import PostsService from "@/service/PostsService";

    export default {
        data(){
            return{
                User: this.$store.getters['registration/User'],
                subscribes: [],
            }
        },
        mounted() {
            try {
                PostsService.getUserSubscribes(this.User.id).then(p =>
                    {
                        this.subscribes = p.data
                    }
                )
            }
            catch (e) {
                console.log(e)
            }
            console.log(this.$store.getters['registration/User'])
        },
        methods: {
            unsubscribe(e){
                const index = this.subscribes.indexOf(e)
                this.subscribes.splice(index, 1)
            }
        }
    }
</script>

<style scoped>
.leftBar{
    top: 50px;
    right: 50px;
    border: #2c3e50 solid;
    width: 40%;
    height: 50% ;
    padding: 10px;

}

.sting{
    text-align: left;
    margin-bottom: 5px;
}
.common{
    display: block;
}

.bottomBar{
    margin-top: 10px;
        float: left;
    border: #2c3e50 solid;
    width: 45%;
}

.rightBar{
    margin-top: 10px;
    float: right;
    border: #2c3e50 solid;
    width: 45%;
}
    .list{
        height: 100%;
    }

.row{
    border: #2c3e50 solid 1px;
}


</style>