<template>
  <div class="home">
    <div>
      <h1 class="task">
        Понедельник
      </h1>
      <div class="task">
        {{ getDate('1').toLocaleDateString() }}
      </div>
    </div>
    <tasks-list :tasks="MondayTasks" @taskChose="taskChose($event)"></tasks-list>
  </div>

  <div class="home">
    <h1 class="task">Вторник</h1>
    <div class="task">
      {{getDate('2').toLocaleDateString() }}
    </div>
    <tasks-list :tasks="TuesdayTasks" @taskChose="taskChose($event)"></tasks-list>
  </div>

  <div class="home">
    <h1 class="task">Среда</h1>
    <div class="task">
      {{getDate('3').toLocaleDateString()}}
    </div>
    <tasks-list :tasks="WednusdayTasks" @taskChose="taskChose($event)"></tasks-list>
  </div>

  <div class="home">
    <h1 class="task">Четверг</h1>
    <div class="task">
      {{getDate('4').toLocaleDateString()}}
    </div>
    <tasks-list :tasks="ThursdayTasks" @taskChose="taskChose($event)"></tasks-list>
  </div>

  <div class="home">
    <h1 class="task">Пятница</h1>
    <div class="task">
      {{getDate('5').toLocaleDateString()}}
    </div>
    <tasks-list :tasks="FridayTasks" @taskChose="taskChose($event)"></tasks-list>
  </div>

  <div class="home">
    <h1 class="task">Суббота</h1>
    <div class="task">
      {{getDate('6').toLocaleDateString()}}
    </div>
    <tasks-list :tasks="SaturdayTasks" @taskChose="taskChose($event)"></tasks-list>
  </div>

  <div class="home">
    <h1 class="task">Восресенье</h1>
    <div class="task">
      {{ getDate('0').toLocaleDateString() }}
    </div>
    <tasks-list :tasks="SundayTasks" @taskChose="taskChose($event)"></tasks-list>
  </div>

  <my-dialog v-model:show="show" :user="this.user" :task="this.tasks.find(t => t.id == chosenTaskId)">

  </my-dialog>

</template>

<script>
// @ is an alias to /src

import {mapActions} from "vuex";
import PostsService from "../service/PostsService";
import MyDialog from "@/components/UI/MyDialog";
import stringify from "json5/lib/stringify";

export default {
  name: 'Home',
  components: {MyDialog},
  data(){
    return{
      posts: [],
      tasks: [],
      user: {},
      show: false,
      chosenTaskId: null
    }
  },
  methods:{
    ...mapActions({
        login: 'registration/login',
        get: 'users/getUsers'
    }),
    async getPosts(){
      try {
        const response = await PostsService.getPosts();
        console.log(response.data);
        this.posts = response.data;
      } catch (e) {
        console.log(e.response?.data?.message);
      }
    },
    async getTasks(){
      try {
        const response = await PostsService.getTasks();
        this.tasks = response.data;
      } catch (e) {
        console.log(e.response?.data?.message);
      }
    },
    taskChose(e){
      this.chosenTaskId = e;
      this.user = this.$store.getters['registration/User'];
      this.show = true;
    },
    getDate(id) {
      const date = new Date()
      let ret;

      const offset = date.getDay() - id
      if (date.getDay() >= id)
        ret = new Date(`${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate() + 7 - offset}`)
      else
        ret = new Date(`${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate() - offset}`)
      return ret
    },

  },
  computed: {
    MondayTasks() {
      return [...this.tasks].filter(facility => facility.day=="1")
    },
    TuesdayTasks() {
      return [...this.tasks].filter(facility => facility.day=="2")
    },
    WednusdayTasks() {
      return [...this.tasks].filter(facility => facility.day=="3")
    },
    ThursdayTasks() {
      return [...this.tasks].filter(facility => facility.day=="4")
    },
    FridayTasks() {
      return [...this.tasks].filter(facility => facility.day=="5")
    },
    SaturdayTasks() {
      return [...this.tasks].filter(facility => facility.day=="6")
    },
    SundayTasks() {
      return [...this.tasks ].filter(facility => facility.day=="0")
    },
  },
  mounted() {
      const obj = {
        name: "name",
        etwe: "rew"
      }
      const reObj = JSON.stringify(obj)
      console.log(reObj)
      console.log(JSON.parse(reObj))
      this.user = this.$store.getters['registration/User'];
      this.getTasks();
  }
}
</script>

<style scoped>
  .home{
    float: left;
    width: calc(1/8 * 100%);
    margin-left: 20px;
  }
  .task {
    border: #f6cbba solid 1px;
    margin-bottom: 10px;
  }
</style>
