<template>
    <div>
        <h1>Вход</h1>
        <div>
            <my-input class="inpt" placeholder="classroom" v-model="classroomName"></my-input>
        </div>
        <div>
            <my-input class="inpt"  v-model="facility.num"></my-input>
        </div>
        <div v-for="facility in facilities"
             :key="facility.id"
        >
            {{facility.num}}
        </div>
        <div>
            <my-button class="btn" @click="addFacilityTab">Добавить установку</my-button>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                classroomName: '',
                facilities: [],
                facility: {
                    id: 0,
                    num: 1
                },
                count: 0

            }
        },
        methods:{
            addFacilityTab(){
                const obj = {}
                obj.id = this.count
                obj.num = this.facility.num
                this.facilities.push(obj)
                this.count++
            }
        }
    }
</script>

