<template>
    <div>
        <workshop
                v-for="workshop in workshops"
                :workshop="workshop"
                :key="workshop.id"
                @workshopChoose="workshopChoose($event)"
        >

        </workshop>
    </div>
</template>

<script>
    import Workshop from "@/components/Workshop";
    export default {
        name: 'workshop-list',
        components: {Workshop},
        props:{
            workshops: {
                type: Array,
                required: true
            }
        },
        methods: {
            workshopChoose(e){
                this.$emit('workshopChoose', e);
            }
        }
    }
</script>

