<template>
    <div class="post">
        <subscribe
                v-for="subscribe in subscribes"
                :subscribe="subscribe"
                :key="subscribe.id"
                @unsubscribe="unsubscribe"
        >

        </subscribe>
    </div>
</template>

<script>
    import Subscribe from "@/components/Subscribe";
    export default {
        name: 'subscribe-list',
        components: {Subscribe},
        props:{
            subscribes: {
                type: Array,
                required: true
            }
        },
        methods: {
            unsubscribe(e){
                this.$emit('unsubscribe', e)
            }
        }

    }
</script>

<style scoped>
    .post{
        display: flex;
    }
</style>