<template>
    <div class="dialog" v-if="show" @click.stop="hideDialog">
        <div @click.stop class="dialog__content">
            <slot></slot>
            <div class="task">Описание задачи: {{ task.description }}</div>
            <div class="task">Количество свободных мест: {{ task.numOfPoints }}</div>
            <div class="task">Записаться на: {{ date.toLocaleDateString() }}</div>
            <my-button @click="subscribe">Записаться</my-button>
        </div>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService";

    export default {
        name: 'my-dialog',
        props: {
            show: {
                type: Boolean,
                default: false
            },
            task: {
                type: Object,
                required: true
            },
            user: {
                type: Object,
                required: true
            }
        },
        methods: {
            hideDialog() {
                this.$emit('update:show', false)
            },
            async subscribe(){
                console.log(this.task)
                console.log(this.user)
                try {
                    const data = {
                        userId: this.user.id,
                        taskId: this.task.id,
                        date: this.date.toISOString()
                    }
                    const resp = await PostsService.postLabUser(data)
                }
                catch (e) {
                    console.log(e)
                }
            }
        },
        computed: {
            date(){
                const date = new Date()
                let ret;
                const offset = date.getDay() - this.task.day

                if (this.task.day <= date.getDay())
                    ret = new Date(`${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate() + 7 - offset}`)
                else
                    ret = new Date(`${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate() - offset}`)
                return ret
            }
        }

    }
</script>

<style scoped>
    .dialog {
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.5);
        position: fixed;
        display: flex;
    }
    .dialog__content {
        margin: auto;
        background: white;
        border-radius: 12px;
        min-height: 50px;
        min-width: 300px;
        padding: 20px;
    }
    .task{
        margin: 5px;
    }
</style>