<template>
    <div>
        <div class="Post" @click="workshopChoose">
            <div class="line">Номер аудитории: {{ workshop.сlassroomName }}</div>
            <div class="line">Id: {{  workshop.id }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "workshop",
        props: {
            workshop: {
                type: Object,
                required: true
            },
        },
        methods: {
            workshopChoose(){
                this.$emit('workshopChoose', this.workshop.id.toString())
            }
        }
    }
</script>

<style scoped>
    .line{
        margin-bottom: 5px;
    }
    .Post{
        float: left;
        padding: 10px 15px;
        width: 20%;
        border: 2px solid darksalmon;
        margin-bottom: 15px;
        margin-right: 25px;
        margin-left: 25px;
    }
    .Post:hover{
        background-color: #f6cbba;
    }
</style>