import { createRouter, createWebHistory, createWebHashHistory } from 'vue-router'
import store from '../store'
import Home from '../views/Home.vue'
import Registration from '../views/Registration'
import Register from '../views/Register'
import Test from '../views/Test'
import Students from "@/views/Students";
import Schedule from "@/views/Schedule";
import WorkshopCreate from "@/views/WorkshopCreate";
import FacilityCreate from "@/views/FacilityCreate";
import TasksCreate from "@/views/TasksCreate";
import Activate from "@/views/Activate";
import Profile from "@/views/Profile";

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/registration',
    name: 'Registration',
    component: Registration
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/test',
    name: 'Test',
    component: Test
  },
  {
    path: '/studentslist',
    name: 'studentsList',
    component: Students,
    meta: {
      requiresAuth: true,
      accessRole: 'admin'
    }
  },
  {
    path: '/schedule',
    name: 'schedule',
    component: Schedule,
    meta: {
      requiresAuth: true,

    }
  },
  {
    path: '/workshop',
    name: 'workshop',
    component: WorkshopCreate,
    meta: {
      requiresAuth: true,
      accessRole: 'admin'
    }
  },
  {
    path: '/facility',
    name: 'facility',
    component: FacilityCreate,
    meta: {
      requiresAuth: true,
      accessRole: 'admin'
    }
  },
  {
    path: '/tasks',
    name: 'tasks',
    component: TasksCreate,
    meta: {
      requiresAuth: true,
      accessRole: 'admin'
    }
  },
  {
    path: '/activate/:link',
    name: 'activate',
    component: Activate
  },
  {
    path: '/profile',
    name: 'profile',
    component: Profile,
    meta: {
      requiresAuth: true
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters['registration/isAuthed']) {
      next()
      return
    }
    next('/registration')
  } else {
    next()
  }
})

export default router
