<template>
    <div>
        <h1>Постановка задач</h1>
        <div>
            <my-select v-model="Day"></my-select>
        </div>
        <div>
            <my-input placeholder="Описание задачи" v-model="TaskDescription"></my-input>
        </div>
        <div>
            <my-input placeholder="id установки" v-model="FacilityId"></my-input>
        </div>
        <div>
            <my-button @click="registration()">Зарегистрировать</my-button>
        </div>
        <div>
            <div>
                <my-input class="search" placeholder="Название установки" v-model="SearchFacility"></my-input>
            </div>
            <div>
                <facility-list :facilities="sortedAndSearchedPosts" @chooseFacility="chooseFacility($event)"></facility-list>
            </div>
        </div>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService"
    import FacilityList from "@/components/FacilityList";

    export default {
        data(){
            return{
                Day: "Понедельник",
                TaskDescription: "",
                NumOfFacilities: "",
                FacilityId: "",
                SearchFacility: "",
                Facilities: [],
                DayOfWeek: {
                    "Воскресенье" : 0,
                    "Понедельник" : 1,
                    "Вторник" : 2,
                    "Среда" : 3,
                    "Четверг" : 4,
                    "Пятница" : 5,
                    "Суббота" : 6
                }
            }
        },
        methods: {
            async registration(){
                try {
                    const responseLab = await PostsService.postLab({facilityId: this.FacilityId, day: this.DayOfWeek[this.Day]})
                    const labId = responseLab.data.id
                    const responseTask = await PostsService.postTask({description: this.TaskDescription, labId: labId})
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            async getFacilities(){
                try {
                    const response = await PostsService.getFacilitiess()
                    this.Facilities = response.data
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            chooseFacility(e){
                this.FacilityId = e.toString();
            }
        },
        mounted() {
            this.getFacilities();
        },
        computed: {
            sortedAndSearchedPosts() {
                return [...this.Facilities].filter(facility => facility.name.toLowerCase().includes(this.SearchFacility.toLowerCase()))
            }
        }

    }
</script>

<style scoped>
    .search{
        width: 80%;
        margin-top: 30px;
        margin-bottom: 30px;
    }
</style>