<template>
    <div>
        <h1>Регистрация аудитории</h1>
        <div>
            <my-input class="inpt" placeholder="Название аудитории" v-model="WorkshopName"></my-input>
        </div>
        <div>
            <my-button class="btn" @click="registration()">Зарегистрировать</my-button>
        </div>

    </div>
</template>

<script>
    import PostsService from "@/service/PostsService";

    export default {
        data(){
            return{
                WorkshopName: "",
            }
        },
        methods: {
            async registration(){
                try {
                    const response = await PostsService.postWorkshop(this.WorkshopName);
                    console.log(response)
                } catch (e) {
                    console.log(e.response?.data?.message);
                }
            }
        }

    }
</script>

