<template>
    <div class="all">
        <h1>Вход</h1>
        <div>
            <my-input class="inpt" placeholder="login" v-model="userLogin"></my-input>
        </div>
        <div>
            <my-button class="btn" @click="Login({email: this.userLogin})">Войти</my-button>
        </div>
        <div>
            <my-router-link to="/register">Зарегистрироваться</my-router-link>
        </div>
        <div style="margin-top: 10px">
            {{  message }}
        </div>
    </div>
</template>

<script>
import {mapActions} from "vuex";

export default {
    data(){
        return{
            tokenKey: 'accessToken',
            userLogin: '',
            password: '',
            message: ''
        }
    },
    methods: {
        ...mapActions({
            login: 'registration/login',
            logout: 'registration/logout',

        }),
        async Login(email){
            const resp = await this.login(email)
            this.message = resp;
        },
        // отпавка запроса к контроллеру AccountController для получения токена
        async getTokenAsync() {
            // отправляет запрос и получаем ответ
            const response = await fetch("https://localhost:5001/token", {
                method: "POST",
                headers: {"Content-Type": "application/json"},
                body: JSON.stringify({"username": this.userLogin,"password":this.password}),
            });
            // получаем данные
            const data = await response.json();

            // если запрос прошел нормально
            if (response.ok === true) {

                // сохраняем в хранилище sessionStorage токен доступа
                sessionStorage.setItem(this.tokenKey, data.access_token);
                console.log(data);
            }
            else {
                // если произошла ошибка, из errorText получаем текст ошибки
                console.log("Error: ", response.status, data.errorText);
            }
        },
        // отправка запроса к контроллеру ValuesController
        async getData(url) {
            const token = sessionStorage.getItem(this.tokenKey);

            const response = await fetch(url, {
                method: "GET",
                headers: {
                    "Accept": "application/json",
                    "Authorization": "Bearer " + token  // передача токена в заголовке
                }
            });
            if (response.ok === true) {

                const data = await response.json();
                console.log(data)
            }
            else
                console.log("Status: ", response.status);
        },
        async PostWork() {
            const url = "https://localhost:5001/api/users";
            const response = await fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: '{"login":"admin","password":"12345678","role":"admin"}'
            });
            if (response.ok === true) {

                const data = await response.json();
                console.log(data)
            }
            else
                console.log("Status: ", response.status);
        },
    }
}
</script>

<style scoped>
    .btn{
        width: 200px;
        padding: 5px 10px;
    }
    .inpt{
        width: 180px;
        padding: 10px 10px;
    }
</style>