<template>
    <h1>Students</h1>
    <div class="post">
        <Student
                v-for="student in students"
                :student="student"
                :key="student.id"
                @chooseStudent="chooseStudent($event)"
        >

        </Student>
    </div>
</template>

<script>
    import Student from "@/components/Student";
    export default {
        name: 'student-list',
        components: {Student},
        props:{
            students: {
                type: Array,
                required: true
            }
        },
        methods: {
            chooseStudent(e){
                this.$emit('chooseStudent', e)
            }
        }

    }
</script>

<style scoped>
    .post{
        display: flex;
    }
</style>