<template>
    <div>
        <select v-model="modelValue" @input="updateInput" class="select">
            <option>Понедельник</option>
            <option>Вторник</option>
            <option>Среда</option>
            <option>Четверг</option>
            <option>Пятница</option>
            <option>Суббота</option>
            <option>Воскресенье</option>
        </select>
    </div>
</template>

<script>
    export default {
        name: 'my-select',
        props: {
            modelValue: String
        },
        methods: {
            updateInput(event) {
                this.$emit('update:modelValue', event.target.value)
            }
        }
    }
</script>

<style scoped>
.select{
    display: inline-block;
    width: 231.6px;
    padding: 10px 15px;
    margin-bottom: 10px;
    border: 1px solid darksalmon;
    background-color: white;
}
</style>