<template>
        <input :value="modelValue" @input="updateInput">
</template>

<script>
    export default {
            name: 'my-input',
            props: {
                    modelValue: String
            },
            methods: {
                    updateInput(event) {
                            this.$emit('update:modelValue', event.target.value)
                    }
            }
    }
</script>

<style scoped>
input {
        display: inline-block;
        width: 200px;
        padding: 10px 15px;
        margin-bottom: 10px;
        border: 1px solid darksalmon;
}
input:focus {
        outline: darksalmon solid thin;
}
</style>