<template>
    <div>
        <div class="Post" @click="chooseFacility">
            <div class="line">Id: {{  facility.id }}</div>
            <div class="line">Название: {{  facility.name }}</div>
            <div class="line">Число мест: {{  facility.numOfPoints }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "facility",
        props:{
            facility: {
                type: Object,
                required: true
            }
        },
        methods:{
            chooseFacility(){
                this.$emit('chooseFacility', this.facility.id.toString() )
            }
        },
        data(){
            return{
                NumOfPoints: Number,
            }
        },

    }
</script>

<style scoped>
    .line{
        margin-bottom: 5px;
    }
    .Post{
        float: left;
        padding: 10px 15px;
        width: 20%;
        border: 2px solid darksalmon;
        margin-bottom: 15px;
        margin-right: 25px;
        margin-left: 25px;
    }
    .Post:hover{
        background-color: #f6cbba;
    }
</style>