<template>
    <div>
        <task
                v-for="task in tasks"
                :task="task"
                :key="task.id"
                @taskChose="ChooseTask"
        >

        </task>
    </div>
</template>

<script>
    import Task from "@/components/Task";

    export default {
        name: 'tasks-list',
        components: {Task},
        props: {
            tasks: {
                type: Array,
                required: true
            }
        },
        methods: {
            ChooseTask(id){
                this.$emit("taskChose", id)
            }
        }
    }
</script>

