<template>
    <div>
        <div class="Post" @click="ChooseTask">
            <div class="line">Описание задачи: {{ task.description }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "task",
        props: {
            task: {
                type: Object,
                required: true
            },
        },
        methods: {
            ChooseTask(){
                this.$emit("taskChose", this.task.id)
            }
        }
    }
</script>

<style scoped>
    .line{
        margin-bottom: 5px;
    }
    .Post{
        float: bottom;
        padding: 10px 15px;
        border: 2px solid darksalmon;
        margin-top: 35px;
        margin-right: 25px;
        margin-left: 25px;
        margin-bottom: 15px;
    }
    .Post:hover{
        background-color: #f6cbba;
    }

</style>