<template>
    <div class="Post" @click="unsubscribe">
        <div class="line">Дата: {{ new Date(subscribe.date).toLocaleDateString() }}</div>
        <div class="line">Задача: {{ subscribe.task.description }}</div>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService";

    export default {
        name: 'subscribe',
        props: {
            subscribe: {
                type: Object,
                required: true
            },
        },
        methods: {
            async unsubscribe(){
               try {
                    const resp = await PostsService.unsubscribe(this.subscribe.id)
                   this.$emit('unsubscribe', this.subscribe.id)
               }
               catch (e) {
                   console.log(e)
               }
            }
        }
    }
</script>

<style scoped>
    .mbtn{
        width: unset;
        height: unset;
    }
    .line{
        margin-bottom: 5px;
    }
    .Post{
        padding: 10px 15px;
        width: 25%;
        border: 2px solid teal;
        margin-bottom: 15px;
        margin-right: 15px;
    }

    .Post:hover{
        background-color: #f6cbba;
    }
</style>