<template>
    <div class="Post" @click="chooseStudent">
        <div class="line">ФИО: {{ student.name + ' ' + student.surname + ' ' + student.secondName}}</div>
        <div class="line">Email: {{ student.email }}</div>
    </div>
</template>

<script>
    export default {
        name: 'student',
        props: {
            student: {
                type: Object,
                required: true
            },
        },
        methods: {
            chooseStudent(){
                this.$emit('chooseStudent', student.id)
            }
        }
    }
</script>

<style scoped>
    .mbtn{
        width: unset;
        height: unset;
    }
    .line{
        margin-bottom: 5px;
    }
    .Post{
        padding: 10px 15px;
        width: 25%;
        border: 2px solid teal;
        margin-bottom: 15px;
        margin-right: 15px;
    }
</style>