<template>
    <div>
        <h1>Регистрация установки</h1>
        <div>
            <my-input placeholder="Число мест на установке" v-model="NumOfPoints"></my-input>
        </div>
        <div>
            <my-input placeholder="Название установки" v-model="Name"></my-input>
        </div>
        <div>
            <my-input placeholder="id Аудитории" v-model="WorkshopId"></my-input>
        </div>
        <div>
            <my-button @click="registration()">Зарегистрировать</my-button>
        </div>
        <div>
            <div>
                <my-input class="search" placeholder="Название аудитории" v-model="SearchWorkshop"></my-input>
            </div>
            <div>
                <workshop-list :workshops="sortedAndSearchedPosts" @workshopChoose="workshopChoose($event)"></workshop-list>
            </div>
        </div>
    </div>
</template>

<script>
    import PostsService from "@/service/PostsService"

    export default {
        data(){
            return{
                Name: "",
                NumOfPoints: "",
                NumOfFacilities: "",
                WorkshopId: "",
                SearchWorkshop: "",
                Workshops: []
            }
        },
        methods: {
            async registration(){
                try {
                    // let arr = [];
                    // for (let i = 0; i < this.NumOfFacilities; i++){
                    //     arr.push(PostsService.postFacility({num: this.NumOfPoints, workshopId: this.WorkshopId, name: this.Name}))
                    // }
                    // const response = await Promise.all(arr)
                    const response = await PostsService.postFacility({num: this.NumOfPoints, workshopId: this.WorkshopId, name: this.Name})
                    console.log(response)
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            async getWorkshops(){
                try {
                    const response = await PostsService.getWorkshops()
                    this.Workshops = response.data
                    console.log(response)
                } catch (e) {
                    console.log(e.response?.data?.message)
                }
            },
            workshopChoose(e){
                this.WorkshopId = e;
            }
        },
        mounted() {
                this.getWorkshops();
        },
        computed: {
            sortedAndSearchedPosts() {
                return [...this.Workshops].filter(workshop => workshop.сlassroomName.toLowerCase().includes(this.SearchWorkshop.toLowerCase()))
            }
        }

    }
</script>

<style scoped>
.search{
    width: 80%;
    margin-top: 30px;
    margin-bottom: 30px;
}
</style>