<template>
    <div>
        <facility
                v-for="facility in facilities"
                :facility="facility"
                :key="facility.id"
                @chooseFacility="chooseFacility($event)"
        >

        </facility>
    </div>
</template>

<script>
    import Facility from "@/components/Facility";
    export default {
        name: 'facility-list',
        components: {Facility},
        props:{
            facilities: {
                type: Array,
                required: true
            }
        },
        methods:{
            chooseFacility(e){
                this.$emit('chooseFacility', e)
            }
        }
    }
</script>

