import $api from "../http";

export default class AuthService {
    static async login(email){
        return $api.post('/token/authenticate', {email})
    }

    static async registration(data) {
        return $api.post('/token/register', {email: data.email, name: data.name, surname: data.surname, secondname: data.secondname})
    }

    static async activate(link){
        return $api.get('/token/activate/' + link)
    }

    static async logout() {
        return $api.post('/logout')
    }

}